import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import { Col, Empty, Modal, Tooltip, Row } from "antd";
import getLanguage from "../../../App/MyLanguange";

class ModalSearchCustomer extends Component {
  state = {};

  handleCancel = e => {
    this.props.handleModal();
  };

  handleOk = e => {
    this.props.handleModal();
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  

  render() {

    let columns = [
      {
        title: getLanguage({id: "customerCode"}),
        dataIndex: "customer_code",
        rowKey: "customer_code_readonly",
        width: "40%",
        render: text => (
          <React.Fragment>
            <Tooltip
              placement="bottom"
              title={<IntlMessages id="tooltip.select" />}
            >
              <a
                onClick={e => {
                  e.preventDefault();
                  this.handleSelect(text);
                }}
                href="javascript:;"
                style={{ marginRight: "15px" }}
              >
                {text}
              </a>
            </Tooltip>
          </React.Fragment>
        )
      },
      {
        title: getLanguage({id: "customerName"}),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "60%"
      }
    ];
    var dataSource = [];
    if (this.props.customersData) {
      const customersData = this.props.customersData;
      // map data dari state global ke dataSource: Table
      Object.keys(customersData).map((item, index) => {
        return dataSource.push({
          ...customersData[item],
          key: item
        });
      });
    }
    // const { idLocation } = this.props;
    // var loc, locName;
    // if (this.props.locations && idLocation) {
    //   loc = this.props.locations.filter(function(data) {
    //     return data.id_location === idLocation;
    //   })[0];
    //   locName = loc.location_name;
    // }

    return (
      <Modal
        centered
        footer={null}
        onOk={this.handleOk}
        onCancel={this.handleCancel}
        style={{ marginTop: "20px" }}
        title={<IntlMessages id="sidebar.customer" />}
        visible={this.props.visible}
      >
        <Row>
          <Col>
            {/* {this.props.idLocation ? (
              dataSource.length === 0 ? (
                <React.Fragment>
                  <p>
                    <IntlMessages id="priceListAvailableLoc" /> :{" "}
                    <strong>{locName}</strong>
                  </p>
                  <Empty
                    description={<IntlMessages id="customers.table.noData" />}
                  />
                </React.Fragment>
              ) : (
                <React.Fragment>
                  <p>
                    <IntlMessages id="priceListAvailableLoc" /> :{" "}
                    <strong>{locName}</strong>
                  </p>
                  <TableWrapper
                    className="invoiceListTable"
                    columns={columns}
                    dataSource={dataSource}
                  />
                </React.Fragment>
              )
            ) : (
              <Empty
                description={
                  <IntlMessages id="purchaseOrders.item.noLocation" />
                }
              />
            )} */}
            {dataSource.length === 0 ? (
              <Empty
                description={<IntlMessages id="customers.table.noData" />}
              />
            ) : (
                <TableWrapper
                  className="invoiceListTable"
                  columns={columns}
                  dataSource={dataSource}
                />
              )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default ModalSearchCustomer;
