import React, { Component } from "react";
import NumberFormat from "../../../App/NumberFormat";

import Button from "../../../../components/uielements/button";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import { Form, Popconfirm, Tooltip} from "antd";
import CardWrapper from "../../../App/app.style";
import "../../../App/editableStyle.css";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  state = {};

  listAction = {
    title: "",
    dataIndex: "operation",
    rowKey: "id_item",
    width: "5%",
    align: "left",
    fixed: "right",
    render: (text, row) => (
      <div>
        {/* Delete Tooltip */}
        <Popconfirm
          title={<IntlMessages id="confirmDelete" />}
          okText={<IntlMessages id="yes" />}
          cancelText={<IntlMessages id="no" />}
          onConfirm={() => this.props.handleDeleteItem(row.index_no)}
        >
          <Tooltip
            title={<IntlMessages id="delete" />}
          >
            <Button
              //style={{ borderRadius: "50%" }}
              className="invoiceDltBtn"
              size="small"
            >
              <i className="ion-trash-b" />
            </Button>
          </Tooltip>
        </Popconfirm>
        {/* / */}
      </div>
    )
  };

  handleSave = row => {
    this.props.handleUpdateItem(row);
  };

  render() {
    let columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        rowKey: "index_no",
        width: "5%"
        // fixed: "left"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        rowKey: "item_code",
        width: "10%"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        rowKey: "item_name",
        width: "40%"
      },
      {
        title: getLanguage({ id: "sidebar.unit" }),
        dataIndex: "unit_name",
        rowKey: "unit_name",
        width: "40%",
        render: (text, row) =>
          text ? text : row.unit ? row.unit.unit_name : null
      },
      {
        title: getLanguage({ id: "costPerUnit" }),
        dataIndex: "current_price",
        rowKey: "current_price",
        width: "10%",
        align: "center",
        render: (text, row) => {
          if (text) {
            return <NumberFormat value={text} />;
          } else if (row.location_price) {
            return <NumberFormat value={row.location_price} />;
          } else {
            return "0";
          }
        }
      },
      {
        title: getLanguage({ id: "price" }),
        dataIndex: "item_price",
        rowKey: "item_price",
        width: "18%",
        align: "center",
        editable: () => {
          return this.props.status === 1 ? true : false;
        }
      },
      {
        title: getLanguage({ id: "discount" }),
        dataIndex: "discount",
        rowKey: "discount",
        width: "10%",
        align: "center",
        editable: () => {
          return this.props.status === 1 ? true : false;
        },
        render: text => {
          if (text && text.slice(-1) !== "%") {
            return <NumberFormat value={text} />;
          } else {
            return text;
          }
        }
      }
    ];

    if (this.props.status === 1 && columns.length === 7) {
      columns.push(this.listAction);
    }

    const { dataSource } = this.props;

    // mapping data last sales price
    dataSource.map(item => {
      const { items } = this.props;
      var findItem;
      if (items && items.length > 0) {
        findItem = items.filter(function(data) {
          return data.id_item === item.id_item;
        })[0];
        if (findItem) {
          item.last_sales_price = findItem.last_sales_price;
        }
      }
    });

    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const render_columns = columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          status: this.props.status,
          handleSave: this.handleSave
        })
      };
    });

    return (
      <CardWrapper>
        <TableWrapper
          className="invoiceListTable"
          columns={render_columns}
          components={components}
          dataSource={dataSource}
          rowKey={record => record.index_no}
          pagination={false}
        />
      </CardWrapper>
    );
  }
}

export default EditableTable;
