import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import Button from "../../../../components/uielements/button";
import CustomerPriceListCreateForm from "./createForm";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import notification from "../../../../components/notification";
import PageHeader from "../../../../components/utility/pageHeader";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/promotion/customerPriceList/actions";
import customerTypeActions from "../../../../redux/customer/customerType/actions";

const redirecTo = "/dashboard/promotions/customer-price-lists";
const { storeCustomerType } = customerTypeActions;

class CreateCustomerPriceList extends Component {
  state = {
    visibleAddCustomerType: false
  };

  componentDidMount() {
    const {
      createCustomerPriceList,
      getCustomersForCustomerPriceList
    } = this.props;
    createCustomerPriceList();
    getCustomersForCustomerPriceList();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.customerPriceList.error) {
      notification("error", this.props.customerPriceList.error);
      this.props.clearError();
    }
    if (
      this.props.customerPriceList.success ||
      this.props.customerPriceList.warning
      // this.props.customerPriceList.id_customer_pricelist
    ) {
      this.props.history.replace(
        redirecTo +
          "/update/" +
          this.props.customerPriceList.id_customer_pricelist
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.purchaseOrder.success);
      // this.props.clearSuccess();
    }

    // Customer Type
    if (this.props.customerType.error) {
      notification("error", this.props.customerType.error);
      this.props.clearError();
    }

    if (this.props.customerType.success) {
      const { createCustomerPriceList, clearSuccess } = this.props;
      notification("success", this.props.customerType.success);
      this.handleModalAddCustomerType();
      clearSuccess();
      createCustomerPriceList();
    }
  }

  handleModalAddCustomerType = () => {
    this.setState({
      visibleAddCustomerType: !this.state.visibleAddCustomerType
    });
  };

  handleSubmit = value => {
    const { storeCustomerPriceList } = this.props;
    storeCustomerPriceList(value);
  };

  handleSubmitCustomerType = value => {
    const { storeCustomerType } = this.props;
    storeCustomerType(value);
  };

  render() {
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            // marginBottom={true}
            title={<IntlMessages id="customerPriceLists.button.add" />}
          >
            <Button onClick={() => this.props.history.replace(redirecTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <CustomerPriceListCreateForm
            auth={this.props.auth}
            customerPriceList={this.props.customerPriceList}
            getCustomersForCustomerPriceList={
              this.props.getCustomersForCustomerPriceList
            }
            getItemsForCustomerPriceList={
              this.props.getItemsForCustomerPriceList
            }
            permissions={permissions}
            submit={this.handleSubmit}
            history={this.props.history}
            //Customer Type
            customerType={this.props.customerType}
            visibleAddCustomerType={this.state.visibleAddCustomerType}
            handleModalAddCustomerType={this.handleModalAddCustomerType}
            handleSubmitCustomerType={this.handleSubmitCustomerType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    customerPriceList: state.customerPriceList,
    customerType: state.customerType
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storeCustomerType })
)(CreateCustomerPriceList);
