import React, { Component } from "react";
import NumberFormat from "../../../App/NumberFormat";

import { Form, Input, InputNumber, Tooltip } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";

import { parseDiscount } from "../../../../helpers/helper";

const FormItem = Form.Item;

class EditableCell extends Component {
  state = {
    editing: false
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) { this.toggleEdit(); } else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, handleSave } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      if (values.item_price) {
        if (values.item_price < 0) {
          values.item_price = 1;
        }
      } else if (values.discount) {
        // if in % form
        if (values.discount.slice(-1) === "%") {
          if (values.discount.slice(0, 1) === "-") {
            values.discount = "0%";
          } else if (parseFloat(values.discount) > 100) {
            values.discount = "100%";
          } else {
            values.discount = `${parseDiscount(values.discount)}%`;
          }
        }
        // if in amount
        else if (values.discount.slice(-1) !== "%") {
          if (values.discount.slice(0, 1) === "-") {
            values.discount = 0;
          } else {
            values.discount = parseDiscount(values.discount);
          }
        }
      }
      console.log(values);

      this.toggleEdit();
      handleSave({ ...record, ...values });
    });
  };

  render() {
    const { editing } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      status,
      ...restProps
    } = this.props;
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing && dataIndex === "item_price") {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        style={{ width: "124px" }}
                        ref={node => (this.input = node)}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      />
                    )}
                  </FormItem>
                );
              } else if (editing && dataIndex === "discount" && status === 1) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record[dataIndex]
                    })(
                      <Input
                        style={{ width: "70px" }}
                        ref={node => (this.input = node)}
                        onPressEnter={this.save}
                      />
                    )}
                  </FormItem>
                );
              } else if (!editing && dataIndex === "discount" && status === 1) {
                return (
                  <Tooltip
                    placement="bottom"
                    title={<IntlMessages id="purchaseOrders.discount.help" />}
                  >
                    <div
                      className="editable-cell-value-wrap"
                      onClick={this.toggleEdit}
                      style={{ textAlign: "left" }}
                    >
                      {record[dataIndex]
                        ? record[dataIndex].slice(-1) !== "%"
                          ? <NumberFormat value={record[dataIndex]}/>
                          : record[dataIndex]
                        : "0"}
                    </div>
                  </Tooltip>
                );
              } else if (!editing && status === 1) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={record[dataIndex]}/>
                      : "0"}
                  </div>
                );
              } else if (!editing && status !== 1 && dataIndex === "discount") {
                return (
                  <div
                  // className="editable-cell-value-wrap"
                  // onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? record[dataIndex].slice(-1) !== "%"
                        ? <NumberFormat value={record[dataIndex]}/>

                        : record[dataIndex]
                      : "0"}
                  </div>
                );
              } else if (status !== 1) {
                return record[dataIndex]
                  ? <NumberFormat value={record[dataIndex]}/>
                  : "0";
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
            restProps.children
          )}
      </td>
    );
  }
}

export default EditableCell;
