import React, { Component } from "react";
import moment from "moment";
import Button from "../../../../components/uielements/button";
import EditableTable from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import ModalSearchCustomer from "./modalSearchCustomer";
import NumberFormat from "../../../App/NumberFormat";
import notification from "../../../../components/notification";
import {
  Button as ButtonAntd,
  Checkbox,
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Input,
  Row,
  Select,
  Spin
} from "antd";
import ModalAddCustomerType from "./modalAddCustomerType";
import getLanguage from "../../../App/MyLanguange";
import AddModule from "../../../App/AddModule";

const InputGroup = Input.Group;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const { TextArea } = Input;

class CustomerPriceListCreateForm extends Component {
  state = {
    customer_name: "",
    dataSource: [],
    expired_date: null,
    id_customer: "",
    id_location: "",
    item: null,
    items: [],
    priceListType: parseInt(1),
    search: null,
    searchCust: null,
    selected_items: [],
    start_date: null,
    visibleModalSearchCustomer: false
  };

  componentDidMount() {
    const { user } = this.props.auth;
    let { default_employee_location } = user ? user : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    if (default_employee_location && default_employee_location.id_location) {
      const id_location = default_employee_location.id_location;
      this.handleSelectLocation(id_location);
    }
  }

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["pricelist_code"]: null
      });
    }
  };

  handleChangeRangePicker = (value, dateString) => {
    this.setState({ start_date: dateString[0], expired_date: dateString[1] });
  };

  handleChangeSelectPriceListType = value => {
    const priceListType = value;
    const { getCustomersForCustomerPriceList } = this.props;
    if (this.state.priceListType !== value) {
      if (value === 1) {
        getCustomersForCustomerPriceList();
      }
      this.setState({ priceListType, searchCust: null });
    }
  };

  handleChangeSelectCustomerType = value => {
    this.props.form.setFieldsValue({
      id_customer_type: value
    });
  };

  handleDeleteItem = key => {
    var dataSource = [...this.state.dataSource];
    // console.log(key, dataSource);
    dataSource = dataSource.filter(item => item.index_no !== key);
    var i = 0;
    let selected_items = [];
    dataSource.map(item => {
      selected_items.push(item.id_item + "_" + item.id_unit);
      i = i + 1;
      item.index_no = i;
    });
    this.setState({
      dataSource,
      selected_items
    });
  };

  handleModalSearchCustomer = () => {
    const { visibleModalSearchCustomer } = this.state;
    visibleModalSearchCustomer
      ? this.setState({ visibleModalSearchCustomer: false })
      : this.setState({ visibleModalSearchCustomer: true });
  };

  handleLoading = () => {
    if (this.props.customerPriceList.loading) return true;
    else return false;
  };

  handleSearchCustomer = value => {
    const { getCustomersForCustomerPriceList } = this.props;
    this.setState({ searchCust: value });
    getCustomersForCustomerPriceList(value);
  };

  handleSearchItem = value => {
    const { getItemsForCustomerPriceList } = this.props;
    const { id_location } = this.state;
    if (id_location) {
      this.setState({ search: value });
      getItemsForCustomerPriceList(id_location, value);
    }
  };

  handleSelectCustomer = value => {
    const { searchCust } = this.state;
    let setStateData = {};

    if (searchCust) {
      const { getCustomersForCustomerPriceList } = this.props;
      getCustomersForCustomerPriceList();
      setStateData.searchCust = null;
    }
    setStateData.id_customer = value;
    this.setState(setStateData);
  };

  handleSelectItem = value => {
    if (value) {
      const { items, id_location } = this.props.customerPriceList;
      const { search, selected_items } = this.state;
      let idItem = value.slice(0, value.indexOf("_"));
      let idUnit = value.slice(value.indexOf("_") + 1, value.length);
      this.setState({
        item: items.filter(
          item => item.id_item === idItem && item.id_unit === idUnit
        )[0]
      });

      if (search) {
        const { getItemsForCustomerPriceList } = this.props;
        getItemsForCustomerPriceList(id_location);
        this.setState({ search: null });
      }
      // push ke dataSource
      const { dataSource } = this.state;
      const [searchItem] = dataSource.filter(
        item => item.id_item === idItem && item.id_unit === idUnit
      );
      if (searchItem) {
        // jika data "sudah ada"
      } else {
        const [newItem] = items.filter(
          item => item.id_item === idItem && item.id_unit === idUnit
        );
        const count = dataSource.length + 1;
        this.setState({
          selected_items: [...selected_items, value],
          dataSource: [
            ...this.state.dataSource,
            {
              ...newItem,
              key: newItem.id_item + "_" + newItem.id_unit,
              index_no: count,
              item_price: 0
            }
          ]
        });
      }
    }
  };

  handleSelectLocation = id_location => {
    this.setState({
      id_location,
      dataSource: [],
      search: null,
      selected_items: []
    });
    // const { setFieldsValue } = this.props.form;

    const {
      // getCustomersBasedOnLocation,
      getItemsForCustomerPriceList
    } = this.props;
    getItemsForCustomerPriceList(id_location);
    // getCustomersBasedOnLocation(id_location);
    // setFieldsValue({
    // ["supplier_code"]: null,
    // ["supplier_name"]: null
    // });
  };

  handleSelectModal = value => {
    const { customers } = this.props.customerPriceList;
    const { setFieldsValue } = this.props.form;
    const [find_customer] = customers.filter(
      curr_data => curr_data.customer_code === value
    );
    this.setState({ id_customer: find_customer.id_customer });
    setFieldsValue(
      {
        customer_code: find_customer.customer_code,
        customer_name: find_customer.customer_name
      },
      this.handleModalSearchCustomer()
    );
  };

  handleSubmit = e => {
    e.preventDefault();
    if (this.state.dataSource.length === 0) {
      notification("error", "Item data can not be empty");
      this.props.clearError();
    }
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err && this.state.dataSource.length > 0) {
        value.id_customer_type
          ? (value.id_customer_type = value.id_customer_type)
          : (value.id_customer_type = null);

        value.start_date = moment(this.state.start_date, "DD MMMM YYYY").format(
          "YYYY-MM-DD HH:mm:ssss"
        );
        value.expired_date = moment(
          this.state.expired_date,
          "DD MMMM YYYY"
        ).format("YYYY-MM-DD HH:mm:ssss");

        const { dataSource } = this.state;

        value.customer_pricelist_details = [];

        dataSource.map(item => {
          value.customer_pricelist_details.push({
            current_price: item.location_price,
            id_item: item.id_item,
            id_unit: item.id_unit,
            item_code: item.item_code,
            item_name: item.item_name,
            discount: item.discount || 0,
            item_price: item.item_price || 0
          });
        });
        if (value.customer_pricelist_details.length === 0) {
          notification("error", "Item data can not be empty");
          this.props.clearError();
        } else {
          this.props.submit(value);
        }
      }
    });
  };

  handleUpdateItem = row => {
    const newData = [...this.state.dataSource];
    const index = newData.findIndex(item => row.index_no === item.index_no);
    const item = newData[index];
    newData.splice(index, 1, {
      ...item,
      ...row
    });
    this.setState({ dataSource: newData });
  };

  render() {
    // inline style
    var blockDisplay = {
      display: "block"
    };

    // form layout
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const { search } = this.state;
    const { getFieldDecorator } = this.props.form;
    const {
      customers,
      customer_types,
      id_location,
      items,
      loadingCustomers,
      loadingItems,
      loadingSubmit,
      locations
    } = this.props.customerPriceList;
    const { user } = this.props.auth;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];

    return (
      <>
        <ModalAddCustomerType
          handleModal={this.props.handleModalAddCustomerType}
          customerType={this.props.customerType}
          submit={this.props.handleSubmitCustomerType}
          visible={this.props.visibleAddCustomerType}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <ModalSearchCustomer
            customersData={customers}
            handleModal={this.handleModalSearchCustomer}
            handleSelectModal={this.handleSelectModal}
            idLocation={this.state.id_location}
            locations={locations}
            visible={this.state.visibleModalSearchCustomer}
          />
          {/* <Row style={{ margin: "12px 0px" }}>
          <Col span={20}>
            <Steps size="small" progressDot={customDot} current={0}>
              <Step title="New" />
              <Step title="Confirmed" />
              <Step title="Rejected" />
            </Steps>
          </Col>
        </Row> */}
          {/* Form untuk customer price list data */}
          {/* <Tabs defaultActiveKey="1">
          <TabPane tab={<IntlMessages id="detail" />} key="1">
            
          </TabPane>
        </Tabs> */}
          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="detail" />
          </Divider>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              {/* Form item untuk pricelist code */}
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    label={
                      <IntlMessages id="customerPriceLists.priceListCode" />
                    }
                  >
                    {getFieldDecorator("pricelist_code", {
                      initialValue: "",
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(<Input disabled={this.state.check_code} />)}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              {/* Form item untuk Location */}
              <Form.Item label={<IntlMessages id="sidebar.location" />}>
                {getFieldDecorator("id_location", {
                  initialValue: default_employee_location.id_location
                    ? default_employee_location.id_location
                    : [],
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    filterOption={true}
                    onChange={this.handleSelectLocation}
                    optionFilterProp="children"
                    placeholder={
                      <IntlMessages id="purchaseReceipts.filter.select.location" />
                    }
                    showSearch
                  >
                    {locations ? (
                      locations.map(data => (
                        <Option key={data.id_location} value={data.id_location}>
                          {data.location_code} ({data.location_name})
                        </Option>
                      ))
                    ) : (
                      <Option value="" />
                    )}
                  </Select>
                )}
              </Form.Item>

              <Form.Item label={<IntlMessages id="priceListRangeDate" />}>
                {getFieldDecorator("range_date", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <RangePicker
                    allowClear
                    onChange={this.handleChangeRangePicker}
                    format="DD MMMM YYYY"
                    style={{
                      width: "100%"
                    }}
                  />
                )}
              </Form.Item>
            </div>

            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              {/* Form item untuk pricelist type */}
              <Form.Item
                label={<IntlMessages id="customerPriceLists.priceListType" />}
              >
                {getFieldDecorator("pricelist_type", {
                  initialValue: parseInt(1),
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    onChange={this.handleChangeSelectPriceListType}
                    placeholder={
                      <IntlMessages id="customerPriceLists.priceListType.select" />
                    }
                  >
                    <Option value={1}>
                      {<IntlMessages id="customerCode" />}
                    </Option>
                    <Option value={2}>
                      {<IntlMessages id="sidebar.customerType" />}
                    </Option>
                  </Select>
                )}
              </Form.Item>

              {this.state.priceListType === parseInt(2) ? (
                // {/* Form item untuk Customer Type */}
                <Form.Item label={<IntlMessages id="sidebar.customerType" />}>
                  {// const
                  getFieldDecorator("id_customer_type", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      onChange={this.handleChangeSelectCustomerType}
                      placeholder={
                        <IntlMessages id="customers.filter.select.customerType" />
                      }
                    >
                      {permissions.includes("Create Customer Type") ? (
                        <Option
                          key={"Create Customer Type"}
                          value={undefined}
                          onClick={() =>
                            this.props.handleModalAddCustomerType()
                          }
                        >
                          <div
                            style={{
                              color: "#1890FF",
                              fontStyle: "italic"
                            }}
                          >
                            {" + " +
                              getLanguage({
                                id: "customerTypes.pageHeader"
                              })}
                          </div>
                        </Option>
                      ) : null}

                      {customer_types ? (
                        customer_types.map(data => (
                          <Option
                            key={data.id_customer_type}
                            value={data.id_customer_type}
                          >
                            {data.customer_type_code} ({data.customer_type_name}
                            )
                          </Option>
                        ))
                      ) : (
                        <Option value="" />
                      )}
                    </Select>
                  )}
                </Form.Item>
              ) : (
                // form untuk Customer Code dan Customer Name
                <InputGroup className="isoDetailDataFlex">
                  {/* <Col span={12}>
                  <Form.Item
                    label={
                      <IntlMessages id="customerPriceLists.customerCode" />
                    }
                  >
                    {getFieldDecorator("customer_code", {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Input
                        readOnly={true}
                        onClick={this.handleModalSearchCustomer}
                        addonAfter={
                          <ButtonAntd
                            style={{
                              width: "100%",
                              height: "30px",
                              border: "0px",
                              backgroundColor: "transparent"
                            }}
                            loading={this.handleLoading()}
                            // disabled={loadingTransactions}
                            onClick={this.handleModalSearchCustomer}
                            icon="search"
                          />
                        }
                      />
                    )}
                  </Form.Item>
                </Col> */}
                  <Col span={24}>
                    {/* Form item untuk customer name */}
                    <Form.Item label={<IntlMessages id="sidebar.customer" />}>
                      {getFieldDecorator("id_customer", {
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          // value={searchCust ? searchCust : []}
                          allowClear
                          filterOption={false}
                          loading={loadingCustomers}
                          notFoundContent={
                            loadingCustomers ? (
                              <Spin size="small" />
                            ) : (
                              <Empty
                                description={<IntlMessages id="noData" />}
                              />
                            )
                          }
                          // optionFilterProp="children"
                          onSelect={this.handleSelectCustomer}
                          onSearch={this.handleSearchCustomer}
                          placeholder={<IntlMessages id="select.customer" />}
                          showSearch
                          style={{ width: "100%" }}
                        >
                          {AddModule(
                            "Create Customer",
                            "/dashboard/setup/customers/add",
                            "customers.pageHeader",
                            this.props.history,
                            permissions
                          )}

                          {customers && customers.length > 0
                            ? customers.map(data => (
                                <Option
                                  key={data.id_customer}
                                  value={data.id_customer}
                                >
                                  {data.customer_code} ({data.customer_name})
                                </Option>
                              ))
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              )}

              {/* Form item untuk description */}
              <Form.Item label={<IntlMessages id="suppliers.add.notes" />}>
                {getFieldDecorator("note", {
                  //initialValue: data.description,
                  rules: []
                })(<TextArea rows={4} />)}
              </Form.Item>
            </div>
          </div>

          {/* Form untuk Item Data */}
          <div style={blockDisplay}>
            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="itemDetails" />
            </Divider>
            <div className="isoDetailDataFlex" style={{ padding: "0" }}>
              <div
                className="isoColumn-1 isoFormGroupComp"
                style={{ padding: "0" }}
              >
                <Row style={{ marginBottom: "40px" }}>
                  <Col span={24}>
                    <Form.Item {...formItemLayout}>
                      <Select
                        value={search ? search : []}
                        showSearch
                        allowClear
                        // optionFilterProp="children"
                        filterOption={false}
                        placeholder={
                          <IntlMessages id="purchaseOrders.item.placeholder" />
                        }
                        loading={loadingItems}
                        notFoundContent={
                          loadingItems ? (
                            <Spin size="small" />
                          ) : id_location ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={
                                <IntlMessages id="purchaseOrders.item.noLocation" />
                              }
                            />
                          )
                        }
                        onSelect={this.handleSelectItem}
                        onSearch={this.handleSearchItem}
                      >
                        {AddModule(
                          "Create Item",
                          "/dashboard/setup/items/add",
                          "items.pageHeader",
                          this.props.history,
                          permissions
                        )}

                        {items.map(data =>
                          !this.state.selected_items.includes(
                            data.id_item + "_" + data.id_unit
                          ) ? (
                            <Option
                              key={data.id_item + "_" + data.id_unit}
                              value={data.id_item + "_" + data.id_unit}
                              style={{ width: "100%" }}
                            >
                              <Row>
                                <Col span={14}>
                                  <span>
                                    {data.item_code} ({data.item_name})
                                  </span>
                                </Col>
                                <Col span={10}>
                                  <span>
                                    {data.unit_name} (
                                    <NumberFormat
                                      value={parseFloat(data.unit_conversion)}
                                    />{" "}
                                    {" " + data.base_unit.unit_name})
                                  </span>
                                </Col>
                              </Row>
                            </Option>
                          ) : null
                        )}
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={24}>
                    <EditableTable
                      dataSource={this.state.dataSource}
                      handleDeleteItem={this.handleDeleteItem}
                      handleUpdateItem={this.handleUpdateItem}
                      status={1}
                      //auth={this.props.auth}
                    />
                  </Col>
                </Row>
              </div>
            </div>
          </div>

          {/* Submit Button */}
          <Form.Item>
            <Button
              disabled={
                !permissions.includes("Create Customer Pricelist")
                  ? true
                  : false
              }
              type="primary"
              htmlType="submit"
              // style={{ marginLeft: "50px" }}
              loading={loadingSubmit}
            >
              <IntlMessages id="button.submit" />
            </Button>
          </Form.Item>

          {/* Form */}
        </Form>
      </>
    );
  }
}

const WrappedCustomerPriceListCreateForm = Form.create({
  name: "create_customer_price_list"
})(CustomerPriceListCreateForm);

export default WrappedCustomerPriceListCreateForm;
